<script>
    import App from "./App.svelte";
    import { fade, fly, slide } from 'svelte/transition';
    import { createEventDispatcher } from "svelte";
    const dispatch = createEventDispatcher();

    export let link;
    export let imagem;
    export let nome;
    export let id;

    export let selecionado;
    export let itemsTab1;

    $: uid = itemsTab1.length + 1;

    function remover(){
        dispatch("remover");
    }
    function gravar(e){
        console.log('uid ='+uid);
        dispatch("salvar", {id:uid, nome:nome, link:link, img:imagem, selected:false});
    }
    function editar(e){
        dispatch("editar", {id:id, nome:nome, link:link, img:imagem, selected:false});
    }
    
    let mostrar = false;
    const toggleMostrar = () => (mostrar = !mostrar);
</script>
<style>
    .uk-card{
        transition: all 300ms ease;        
    }
    .uk-card > *{
        max-width: 50vw;
        margin: auto;
    }
    .formeditor{
        display: flex;
        flex-direction: column;
    }
    .formeditor .linha{
        flex: 1;   
        margin: 10px;     
    }
</style>
<div class="uk-scope">
{#if mostrar}
    <div class="uk-card uk-card-primary uk-padding" transition:slide >
        
        <div class="formeditor">
            <div class="uk-inline linha">
                <span class="uk-form-icon" uk-icon="icon: comment"></span>
                <input class="uk-input uk-width-1-1" type="text" bind:value="{nome}" >
                <!-- <input class="uk-input uk-width-1-4" type="text" placeholder="" bind:value="{id}" disabled> -->
            </div>
            <div class="uk-inline linha">
                <span class="uk-form-icon" uk-icon="icon: link"></span>
                <input class="uk-input uk-width-1-1" type="text" bind:value="{link}" >
            </div>
            <div class="uk-inline linha">
                <span class="uk-form-icon" uk-icon="icon: link"></span>
                <input class="uk-input uk-width-1-1" type="text" placeholder="url da imagem" bind:value="{imagem}" >
            </div>

            <div class="uk-grid-small uk-child-width-expand@s uk-text-center uk-margin" uk-grid>
                <button on:click="{gravar}" uk-icon="icon: plus-circle" class="uk-button uk-button-default uk-width-1-1">Adicionar</button>            
                {#if selecionado}
                <button on:click="{editar}" uk-icon="icon: file-edit" class="uk-button  uk-width-1-2" style="background-color: green">Editar</button>
                <button on:click="{remover}" uk-icon="icon: minus-circle" class="uk-button uk-button-danger uk-width-1-2">Remover</button>
                {/if}                      
            </div>
        </div>

    </div>
{/if}
<button uk-icon="icon: plus-circle" class="uk-button uk-button-primary" on:click={toggleMostrar}></button>

</div>